module.exports.getSearchObj = function(str){
	var searchObj = {};
    var qurey;
    qureies = str.replace(/^\?/, '').split('&');
    for(var i =0; i<qureies.length; i++){
        qurey = qureies[i].split('=');
        searchObj[qurey[0]] = qurey[1];
    }
    return searchObj;
};


